"use client";

import { switchBlock, switchFollow } from "@/lib/actions";
import { useOptimistic, useState } from "react";
const UserInfoCardInteraction = ({
  userId,
  isUserBlocked,
  isFollowing,
  isFollowingSent
}) => {
  const [userState, setUserState] = useState({
    following: isFollowing,
    blocked: isUserBlocked,
    followingRequestSent: isFollowingSent
  });
  const follow = async () => {
    switchOptimisticState("follow");
    try {
      await switchFollow(userId);
      setUserState(prev => ({
        ...prev,
        following: prev.following && false,
        followingRequestSent: !prev.following && !prev.followingRequestSent ? true : false
      }));
    } catch (err) {}
  };
  const [optimisticState, switchOptimisticState] = useOptimistic(userState, (state, value) => value === "follow" ? {
    ...state,
    following: state.following && false,
    followingRequestSent: !state.following && !state.followingRequestSent ? true : false
  } : {
    ...state,
    blocked: state.blocked
  });
  const block = async () => {
    switchOptimisticState("block");
    try {
      await switchBlock(userId);
      setUserState(prev_0 => ({
        ...prev_0,
        blocked: !prev_0.blocked
      }));
    } catch (err_0) {}
  };
  return <>
        <form action={follow}>
            <button className="w-full bg-blue-500 text-white text-sm rounded-md p-2">
                {optimisticState.following ? "Following" : optimisticState.followingRequestSent ? "Friend Request Sent" : "Follow"}
            </button>
        </form>
        <form action={block} className="self-end">
            <button className="">
            <span className="text-red-400  text-xs cursor-pointer">
                {optimisticState.blocked ? "Unblock User" : "Block User"}
            </span>
            </button>
       </form>
        </>;
};
export default UserInfoCardInteraction;